import "./styles/main.scss";
import * as bootstrap from "bootstrap";

import "./js/header";
import "./js/footer";

function importAll(r) {
  return r.keys().map(r);
}

const images = importAll(
  require.context(
    "./assets/images",
    false,
    /\.(png|jpg|jpeg|svg|gif|mp4|webm|webp)$/
  )
);

const loaderContainer = document.getElementById("loader-container");
const pageContent = document.getElementById("mainPage");

window.addEventListener("load", () => {
  loaderContainer.classList.add("hidden");
  pageContent.classList.add("visible");

  const scrollToTopBtn = document.getElementById("scroll-to-top");
  scrollToTopBtn.addEventListener("click", () => {
    document.body.scrollTop = document.documentElement.scrollTop = 0;
  });
});

window.addEventListener("scroll", () => {
  if (scrollY >= 800) {
    document.getElementById("scroll-to-top").style.display = "inline-block";
    document.getElementById("scroll-to-top").style.opacity = 1;
  } else {
    document.getElementById("scroll-to-top").style.visibility = "none";
    document.getElementById("scroll-to-top").style.opacity = 0;
  }
});

// window.onscroll = function() {
//   var e;
//   (e = document.getElementById("main-navbar")) && (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80 ? e.style.boxShadow = "0 .3125rem .625rem 0 rgba(0, 0, 0, .12)" : e.style.boxShadow = "none")
// }

window.onscroll = function () {
  try {
    let e;
    (e = document.getElementById("main-navbar")) &&
      (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80
        ? changedNavbar(e)
        : defaultNavbar(e));
  } catch (error) {
    console.error("Navbar not found " + error);
  }
};

function defaultNavbar(e) {
  try {
    e.style.top = "15px";
    e.style.margin = "10px 40px 0px 40px";
    e.style.borderRadius = "12px";
    e.style.padding = "12px 20px";
  } catch (error) {
    throw error;
  }
}

function changedNavbar(e) {
  try {
    e.style.top = "0px";
    e.style.margin = "0px";
    e.style.borderRadius = "0px";
    e.style.padding = "20px 40px";
  } catch (error) {
    throw error;
  }
}

// Smooth Scrolling
try {
  const lenis = new Lenis();

  lenis.on("scroll", ScrollTrigger.update);

  gsap.ticker.add((time) => {
    lenis.raf(time * 1000);
  });

  gsap.ticker.lagSmoothing(0);
} catch (error) {
  throw error;
}

window.addEventListener("load", () => {
  try {
    const entries = document.querySelectorAll(".entry");

    entries.forEach((entry) => {
      let entryMeta = entry.querySelector(".entry__meta");
      let entryMedia = entry.querySelector(".entry__media");

      gsap.set(entryMeta, {
        xPercent: -25,
        opacity: 0,
      });

      gsap.set(entryMedia, {
        xPercent: 100,
        opacity: 0,
      });

      gsap.to(entryMeta, {
        scrollTrigger: {
          trigger: entry,
          start: "top bottom",
          end: "bottom 90%",
          scrub: true,
        },
        xPercent: 0,
        opacity: 1,
      });

      gsap.to(entryMedia, {
        scrollTrigger: {
          trigger: entry,
          start: "top bottom",
          end: "bottom 90%",
          scrub: true,
        },
        xPercent: 0,
        opacity: 1,
      });
    });

    const cardEntries = document.querySelectorAll(".card__entry");

    cardEntries.forEach((entry) => {
      let cardEntryMeta = entry.querySelector(".card__entry__meta");

      gsap.set(cardEntryMeta, {
        xPercent: -10,
        opacity: 0,
      });

      gsap.to(cardEntryMeta, {
        scrollTrigger: {
          trigger: entry,
          start: "top bottom",
          end: "bottom 90%",
          scrub: true,
        },
        xPercent: 0,
        opacity: 1,
      });
    });
  } catch (error) {
    console.error(error);
  }
});
